@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: #BE93C5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #7BC6CC, #BE93C5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
button {
  cursor: pointer;
}
.modal_overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal_container {
    background: rgb(255, 255, 255);
    width: 50%;
    max-width: 32rem;
    padding: 2rem;
    border-radius: 1rem;
}

.modal_container h1 {
    margin-top: 0;
}

.modal_container button {
    background: transparent;
    padding: 0.5rem;
}

@media screen and (max-width: 600px){
    .modal_container {
        width: 80%;
    }
}
.App {
  text-align: center;
  padding: 15px;
}

.center {
  display: flex;
  justify-content: center;
}
.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  width: 100%;
}

@media screen and (max-width: 600px){
  article {
    width: 80vw !important;
    padding: 1rem;
  }
}
.Tilt {
    background: linear-gradient(45deg, #BE93C5, #7BC6CC);
}

.logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo-container img {
    width: 5rem;
}

@media screen and (max-width: 600px) {
    .Tilt {
        display: none;
    }
}
.form {
    width: 700px;
}

@media screen and (max-width: 600px) {
    .pa4 {
        padding: 0 !important;
    }

    .form {
        background: unset;
    }

    .input_container {
        display: flex;
        flex-direction: column;
        box-shadow: unset !important;
        margin-bottom: 30px;
    }

    .input_container button, .input_container input {
        width: auto;
        margin: 0;

    }

    .f3 {
        font-size: 1.25rem !important;
    }
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}

.center img {
    max-width: 90%;
}
